/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  font-family: 'Exo';
  border: 1px solid;
  border-color: #00cc00;
  border-radius: 3px;
  background: none;
  color: #00cc00;
  font-size: 1rem;
  line-height: 1;
  padding: .7rem 1.8rem;
  display: block; }
  @media (min-width: 1080px) {
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"] {
      font-size: 1.2rem;
      padding: 1rem 2.3rem 1rem;
      margin: 0 auto; } }
  button:hover,
  input[type="button"]:hover,
  input[type="reset"]:hover,
  input[type="submit"]:hover {
    color: #006600;
    border-color: #006600; }
  button:active, button:focus,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    color: #00cc00;
    border-color: #00cc00;
    outline: 0; }

/* Fields */
